@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans', sans-serif
}

html,
body {
    height: 100%
}

body {
    margin-top: 30px;
    display: grid;
    background: black;
}

.card {
	color:#fff;
    width: 350px;
    border-radius: 10px;
    background: linear-gradient(145deg, #0E3673, #09244D);
    border: none
}

.neo-button {
    width: 40px;
    height: 40px;
    outline: 0 !important;
    cursor: pointer;
    color: #fff;
    font-size: 15px;
    border: none;
    margin-right: 10px;
    border-radius: 50%;
    background: #F5900C;
}

.no{
    background: black;
}

.num{
	
	color:#eee !important;
}

.line{
	color:#fff;
}

.neo-button:active {
   border-radius: 10%;
background: #F5900C;
}

.fa-facebook {
    color: #3b5998
}

.fa-linkedin {
    color: #0077b5
}

.fa-google {
    color: #dc4e41
}

.fa-youtube {
    color: #cd201f
}

.fa-twitter {
    color: #55acee
}

.profile_button{
	color:#fff;
	padding:10px; 
   border:none;
   outline:0 !important;
    border-radius: 10px;
    background: #F5900C;
	
}

.horario{
    background-color: #F5900C;
}

.horario:hover{
    background-color: #BD6F09;
}
